import './App.css';
import React, { useEffect, useState } from "react"
import Calendar from './components/calendar';
import { nanoid } from 'nanoid';
import sanitizeHtml from 'sanitize-html';

import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';


function TodoCard(props) {
  const [visible, setVisible] = useState(false);

  function onDelete() {
    if (!visible && props.onDelete) props.onDelete()
  }

  useEffect(() => {
    setVisible(true)
  }, [props]);
  return (
    <article className={'relative z-10 w-full px-4 py-2 bg-white shadow shadow-blue-100 rounded-lg shrink-0 flex flex-col group hover:shadow-lg transition-all duration-200 ' + (visible ? 'min-h-[12em] max-h-full opacity-100' : 'min-h-0 max-h-0 opacity-0')} onTransitionEnd={onDelete}>
      <div className='w-full flex gap-4 border-slate-100 border-b'>
        <span className='text-xl overflow-hidden whitespace-nowrap text-ellipsis'>{props.title || ""}</span>
        <button className='px-2 ml-auto opacity-0 group-hover:opacity-100 transition-all' onClick={() => setVisible(false)}>✖</button>
      </div>
      <div className='flex-grow overflow-hidden' dangerouslySetInnerHTML={{ __html: sanitizeHtml(props.todo || "")}}></div>
      <div className='flex gap-4'>
        <span className='text-slate-500 text-sm ml-0 overflow-hidden whitespace-nowrap text-ellipsis'>{props.place || ""}</span>
        <span className='text-slate-500 text-sm mr-0 ml-auto whitespace-nowrap'>{props.date || ""}</span>
      </div>
    </article>
  )
}

export default function App() {
  const [selectedDate, setSelectedDate] = useState(null);
  const [value, setValue] = useState('');
  const [title, setTitle] = useState('');
  const [place, setPlace] = useState('');
  const [todoList, setTodoList] = useState([]);
  const [submitEnable, setSubmitEnable] = useState(false);

  useEffect(() => {
    window.messageEvent['test-message'] = v => {
      console.log('test message')
      console.log(v);
    }
    return () => {
      window.messageEvent['test-message'] = undefined;
    }
  }, []);

  useEffect(() => {
    window.messageEvent['task-added'] = v => {
      console.log("task added ...");
      console.log(v);
      setTodoList(v);
      setValue('');
      setTitle('');
      setPlace('');
    }
    return () => {
      window.messageEvent['task-added'] = undefined;
    }
  }, []);


  useEffect(() => {
    if ((value !== '<p><br></p>' && value !== '') || title !== '' || place !== '') { 
      setSubmitEnable(true);
    }
    else {
      setSubmitEnable(false);
    }
  }, [value, title, place]);

  function submit() {
    let newTodoList = [...todoList, {
      date: selectedDate.format('YYYY-MM-DD'),
      todo: encodeURIComponent(value),
      title: encodeURIComponent(title),
      place: encodeURIComponent(place),
      id: nanoid(7)
    }];
    callNativeApi("add-task", newTodoList);
  }
  
  function saveFile() {
    callNativeApi("save-file", todoList);
  }

  function openFile() {
    callNativeApi("open-file");
  }

  function isWebview() {
    return window.chrome && window.chrome.webview;
  }

  function callNativeApi(eventName, data) {
    if (isWebview()) window.chrome.webview.postMessage({ eventName, data: data || {} });
  }

  return <div className="max-h-screen h-screen w-screen min-h-[600px] min-w-[800px] flex-grow relative bg-slate-100 pt-10" onClick={e => e.stopPropagation()}>
      <header className='bg-blue-500 h-10 w-full absolute top-0 flex items-center'>
        <h1 className='text-white block text-lg my-auto ml-4'>Todo App</h1>
        <button className='bg-blue-500 ml-auto mr-4 my-auto rounded-full text-center border border-white text-md font-thin text-white px-2 hover:bg-white hover:text-blue-500 transition-all' onClick={ saveFile }>save</button>
        <button className='bg-blue-500 mr-4 my-auto rounded-full text-center border border-white text-md font-thin text-white px-2 hover:bg-white hover:text-blue-500 transition-all' onClick={ openFile }>open</button>
      </header>
      <div className='flex h-full p-8 gap-6'>
        <div className="flex flex-col max-w-xl flex-shrink-0 w-full mx-auto h-full max-h-full">
          <div className="h-full flex w-full flex-col gap-8 max-h-full">
            <div className="flex gap-8 w-full">
              <Calendar onSelectDate={setSelectedDate} />
              <div className='flex-grow h-full flex flex-col gap-6'>
                <span className='text-xl font-extrabold mt-4 text-slate-900'>{selectedDate && selectedDate.format('YYYY/MM/DD')}</span>
                <div className='h-full flex flex-col gap-8 border p-2'>
                  <span className='text-slate-500 text-sm'>option</span>
                  <input className='border-b border-slate-300 text-lg text-slate-700 bg-opacity-0 bg-white py-1 px-2 w-full focus:border-orange-300 focus:outline-none' placeholder='title' value={title} onChange={e => setTitle(e.target.value)}/>
                  <input className='border-b border-slate-300 text-lg text-slate-700 bg-opacity-0 bg-white py-1 px-2 w-full focus:border-orange-300 focus:outline-none' placeholder='place' value={place} onChange={e => setPlace(e.target.value)}/>
                </div>
              </div>
            </div>
            <div className="max-h-full h-full flex-shrink min-h-0">
              <div className='w-full h-full flex flex-col max-h-full flex-grow min-h-0 min-w-0'>
                <ReactQuill className='flex flex-col h-full flex-shrink flex-grow-0 max-h-full min-h-0 bg-white' preserveWhitespace theme="snow" value={value} onChange={setValue} />
                <div className='flex'>
                  <button type="button" className='ml-auto px-2 py-0.5 disabled:opacity-20' disabled={!submitEnable} onClick={submit}>
                    <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 0 20 20" width="20px" fill="#334155"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M4.01 6.03l7.51 3.22-7.52-1 .01-2.22m7.5 8.72L4 17.97v-2.22l7.51-1M2.01 3L2 10l15 2-15 2 .01 7L23 12 2.01 3z"/></svg>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <section className='h-full max-h-full overflow-y-auto px-2 flex-grow flex flex-col gap-4 min-w-[20em]'>
          {todoList.length ? 
              todoList.map((v, i) => <TodoCard key={v.id} todo={decodeURIComponent(v.todo)} title={decodeURIComponent(v.title)} place={decodeURIComponent(v.place)} date={v.date} onDelete={() => setTodoList([...todoList.filter((_, j) => i !== j)])}/>)
            : <div className='h-full grid place-items-center text-slate-900'>タスクはありません</div>}
        </section>
      </div>
    </div>
}